import axios from 'axios'

const getAll = () => {
    return axios.get('http://localhost:3001/api/persons')
}

const newPerson = (newObject) => {
    return axios.post('/api/persons', newObject)
}

const deletePerson = (id) => {
    return axios.delete(`/api/persons/${id}`)
}

const NoteService = {
    getAll,
    newPerson,
    deletePerson
}

export default NoteService;